@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,800;1,600;1,800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

.header {
  width: 100vw;
  height: 10vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 4vh;
  letter-spacing: .3vh;
  background-color: #fffaf0;
 box-shadow: 0 8px 6px -6px black;
  z-index: 2;
  position: fixed;
}

.page-title {
  font-size: 3.3vw;
  font-family: 'Playfair Display', serif;
  font-style: italic;
  margin-left: 1vw;
}

.nav-btn-box {
  display: flex;
  justify-content: center;
  align-items: center;
}

.random-button,
.my-gallery-btn {
  margin-right: 2vw;
  width: 12vw;
  height: 6vh;
  font-size: 1.5vw;
  text-align: center;
  font-family: 'Playfair Display', serif;
  letter-spacing: .2vh;
  border-radius: 1vh;
  background-color: #ffff;
  opacity: .7;
  color: black;
}

.my-gallery-btn:hover,
.my-gallery-btn:focus,
.random-button:hover,
.random-button:focus {
  cursor: pointer;
  opacity: .8;
  box-shadow: 0px 0px 1px 1px #9a9a9a;
  transform: scale(1.04);
  transition: transform .3s;
 }

.art {
  max-height: 50vh;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  margin: 2vw;
  border: 7px solid #caa472;
  box-shadow: 3px 9px 5px 0px #9a9a9a, 
    -1px 1px 18px 1px #607d8bbf;
 }

 .art:active,
 .art:hover,
 .art:focus {
  cursor: pointer;
  opacity: .9;
  box-shadow: 0 0 3px 3px #9a9a9a;
  transform: rotate(1deg) scale(1.02);
  transition: transform .3s;
 }

 .main-loading {
  font-family: 'Roboto Mono', monospace;
  font-size: 3vh;
  font-style: italic;
  padding-top: 3vh;
 }
 
.displayed-paintings {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: 100vh;
  justify-content: center;
  margin-top: 8vh;
  padding-top: 3vh;
  position: relative;
  width: 100vw;
  z-index: 1;
}

.hidden {
  display: none;
}

.wrapper {
  height: 100vh;
  width: 100vw;
  overflow-x: hidden;
  overflow-y: auto;
}

.background,
.gallery {
  background-image: url("/static/media/offwhite wallpaper.d5ef1ff0.jpg");
  height: 100vh;
  width: 100vw;
  z-index: -1;
  position: fixed;
}

.parallax::after {
  content: " ";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transform: translateZ(-1px) scale(1.5);
  background-size: 100%;
  z-index: -1;
}

.bg1::after {
  background-image: url("/static/media/offwhite wallpaper.d5ef1ff0.jpg");
  height: 1px;
}

.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.loading-paintings {
  font-size: 2.5vh;
  font-style: italic;
  letter-spacing: .2vh;
  font-family: 'Roboto Mono', monospace;
}

.loading-roll {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}

.loading-roll section {
  -webkit-animation: loading-roll 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: loading-roll 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  transform-origin: 40px 40px;
}

.loading-roll section:after {
  content: " ";
  display: block;
  position: absolute;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: black;
  margin: -4px 0 0 -4px;
}

.loading-roll section:nth-child(1) {
  -webkit-animation-delay: -0.036s;
          animation-delay: -0.036s;
}

.loading-roll section:nth-child(1):after {
  top: 63px;
  left: 63px;
}

.loading-roll section:nth-child(2) {
  -webkit-animation-delay: -0.072s;
          animation-delay: -0.072s;
}

.loading-roll section:nth-child(2):after {
  top: 68px;
  left: 56px;
}

.loading-roll section:nth-child(3) {
  -webkit-animation-delay: -0.108s;
          animation-delay: -0.108s;
}

.loading-roll section:nth-child(3):after {
  top: 71px;
  left: 48px;
}

.loading-roll section:nth-child(4) {
  -webkit-animation-delay: -0.144s;
          animation-delay: -0.144s;
}

.loading-roll section:nth-child(4):after {
  top: 72px;
  left: 40px;
}

.loading-roll section:nth-child(5) {
  -webkit-animation-delay: -0.18s;
          animation-delay: -0.18s;
}

.loading-roll section:nth-child(5):after {
  top: 71px;
  left: 32px;
}

.loading-roll section:nth-child(6) {
  -webkit-animation-delay: -0.216s;
          animation-delay: -0.216s;
}

.loading-roll section:nth-child(6):after {
  top: 68px;
  left: 24px;
}

.loading-roll section:nth-child(7) {
  -webkit-animation-delay: -0.252s;
          animation-delay: -0.252s;
}

.loading-roll section:nth-child(7):after {
  top: 63px;
  left: 17px;
}

.loading-roll section:nth-child(8) {
  -webkit-animation-delay: -0.288s;
          animation-delay: -0.288s;
}

.loading-roll section:nth-child(8):after {
  top: 56px;
  left: 12px;
}

@-webkit-keyframes loading-roll {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes loading-roll {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.hidden {
  display: none;
}
.painting-page,
.painter-page {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
}

.painting-page-title {
  font-size: 3.3vw;
  font-family: 'Playfair Display', serif;
  font-style: italic;
  padding-left: 3.3vw;
  letter-spacing: .17vw;
}

.painting-page-title:hover,
.painting-page-title:active,
.painting-page-title:focus {
  cursor: pointer;
  opacity: .9;
  transform: scale(1.05);
  transition: transform .3s;
  border-radius: 50%;
}

.painting-title {
  font-family: 'Playfair Display', serif;
  font-size: 2.2vw;
  letter-spacing: .1vh;
  max-width: 65vw;
  text-align: center;
}

.artist-name {
  margin: auto;
  font-family: 'Playfair Display', serif;
  font-size: 2.2vw;
  letter-spacing: .1vh;
}

.artist-page-name {
  padding-left: 10vh;
  margin: auto;
  font-family: 'Playfair Display', serif;
  font-size: 2.4vw;
  letter-spacing: .1vh;
}

.save-btn {
  margin-right: 7vh;
  width: 3.4vw;
  opacity: .9;
}

.save-btn:hover,
.save-btn:focus,
.save-btn:active {
  cursor: pointer;
  opacity: .9;
  box-shadow: 0 0 3px 3px #9a9a9a;
  transform: scale(1.01);
  transition: transform .3s;
  border-radius: 10%;
  padding-bottom: .2vh;
}

.painting-nav,
.painter-nav {
  width: 100vw;
  height: 10vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fffaf0;
 box-shadow: 0 8px 6px -6px black;
  z-index: 2;
  position: fixed;
}

.painting-data-container {
  display: flex;
  font-family: 'Roboto Mono', monospace;
}

.painting-box {
  width: 60vw;
  height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 9vh;
  padding-left: 2vh;
}

.artwork {
  max-width: 55vw;
  height: 60vh;
  box-shadow: 3px 9px 5px 0px #9a9a9a, 
  -1px 1px 18px 1px #607d8bbf;
}

.completion-year {
  margin-top: 4vh;
}

.artist-btn {
  color: black;
  font-family: 'Playfair Display', serif;
  font-style: italic;
  font-size: 3.3vw;
  text-align: center;
  margin-right: 3vw;
  margin-left: 3vw;
}

.artist-btn:hover,
.artist-btn:focus,
.artist-btn:active {
  cursor: pointer;
  opacity: .9;
  transform: scale(1.04);
  transition: transform .3s;
  border-radius: 20%;
  margin-right: 3vw;
}

.details-container {
  width: 40vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.artwork-details {
  max-height: 65vh;
}

.summary {
  font-size: 1.5vh;
  padding-right: 4vw;
  overflow: scroll;
}

.detail-title {
  font-weight: bold;
}

.loading-details-message {
  font-size: 2.5vh;
  font-style: italic;
}

.detail-location {
  padding-right: 4vw;
  font-size: 1.4vh;
  font-style: italic;
  padding-bottom: 3vh;
}

.detail-title-location {
  padding-right: 4vw;
  font-size: 1.4vh;
  font-weight: bold;
}

.no-favorites-message {
  font-size: 3vh;
  color: black;
  letter-spacing: .2vh;
  z-index: 10;
  position: absolute;
  text-align: center;
  padding: 34vh;
  margin-left: 28vh;
  font-family: 'Roboto Mono', monospace;
}

@media screen and (max-width: 440px) {
  .art {
    max-width: 90vw;
  }

  .painting-page-title {
    font-size: 5.3vw;
  }

  .artist-btn {
    font-size: 3vh;
    min-width: 80%;
  }
  .summary {
    font-size: 2vh;
  }

  .painting-data-container {
    padding-top: 6vh;
    flex-direction: column;
    align-items: center;
    font-size: auto;
  }

  .painting-title {
    font-size: 2.5vh;
    align-items: right;
  }

  .details-container{
    align-items: center;
    font-size: auto;
  }

  .artwork-details {
    text-align: center;
  }

  .artwork{
    max-width: 55vw;
    height: 60vh;
  }

  .painting-box {
    margin-top: 20%;
    height: 50vh;
    padding-left: 0;
  }

  .save-btn {
    width: 6.5vw;
  }
}

